import {
    Accordion,
    AccordionContent,
    AccordionItem,
    AccordionTrigger,
  } from "@/components/ui/accordion";
  import { MoveRight } from "lucide-react";
  
  const faqData = {
    title: "Frequently asked questions:",
    faqs: [
      {
        question: "Do I need to know how to code?",
        answer:
          "No, you don't need to have any coding experience to get started. Our platform is designed to be user-friendly for beginners.",
      },
      {
        question: "Can I use it for commercial projects?",
        answer:
          "Yes, you can use our platform for commercial projects. We offer different licensing options depending on your needs.",
      },
      {
        question: "Can I use it for multiple projects?",
        answer: "Definitely! Please use it however you like; we don't limit it.",
      },
      {
        question: "Is customer support available?",
        answer: "Yes! Our team is available 24/7 to assist you with any issues.",
      },
    ],
  };
  
  export default function Faq() {
    return (
      <section className="container mx-auto px-4  border-b border-gray-200">
        <div className="text-center mb-12">
          <p className="text-emerald-600 font-medium mb-2">{faqData.title}</p>
          <h2 className="text-5xl font-bold mb-6">Frequently Asked Questions</h2>
        </div>
  
        {/* Added space between questions */}
        <Accordion type="single" collapsible className="max-w-3xl mx-auto space-y-4">
          {faqData.faqs.map((faq, index) => (
            <AccordionItem key={index} value={`faq-${index}`}>
              <AccordionTrigger className="flex items-center gap-4 bg-gray-100 p-4 rounded-md">
                <span className="text-lg font-semibold">{faq.question}</span>
              </AccordionTrigger>
              <AccordionContent className="bg-gray-100 p-4 rounded-md">
                <p className="text-gray-600">{faq.answer}</p>
              </AccordionContent>
            </AccordionItem>
          ))}
        </Accordion>
  
        {/* "Still have a question?" Section */}
        <div className="my-16 ">
          <p className="text-center font-bold">
            <a href="your-link-here" className="text-emerald-600 hover:underline">
              Still have a question?
            </a>
            <MoveRight className="inline ml-2 text-emerald-600" />
          </p>
        </div>
      </section>
    );
  }
  